import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { motion } from "framer-motion";
import { Link } from "react-router-dom"
import { Skeleton, notification } from "antd";
import "antd/dist/antd.min.css";

import axios from "axios";
import { HashConnect } from "hashconnect";
import { AccountId, TokenMintTransaction, TransactionId, TransactionReceipt, TransferTransaction, Hbar } from "@hashgraph/sdk";


import Card from "./nftCard";
import BorrowArrow from "../../assets/images/Rectangle.png"
import Price from "../../assets/images/price.png";
import Sixsense from "../../assets/images/Sixsense.png";
import footerback from "../../assets/images/DOT1.png"

import { Tabs, Tab, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles"
import PropertiesCard from "./propertiesCard";

const useStyles = makeStyles((theme) => ({
  indicator: {
    backgroundColor: 'red', // Customize the color of the tab underline here
    height: "6px"
  },
}));

const tokenId = "0.0.2792760"
const ownId = "0.0.2244085"

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
});


const Buy = (props) => {
  const classes = useStyles();

  const [nft, setNft] = useState(null);
  const [value, setValue] = useState(1);
  const [propertiy, setPropertiy] = useState(4);
  useEffect(() => {
    props.setBuy(true)
  })
  const { ownerId, accountId,  metadata } = useParams();
  console.log(ownerId, accountId, metadata)

  useEffect(() => {
    console.log("getnft function  ")

    const getNFT = async () => {
      const newValue = Buffer.from(metadata)
      const res = await fetch(`https://ipfs.io/ipfs/${newValue}`);
      console.log(res)
      const meta = await res.json();
      console.log(meta)
      meta.image = "https://ipfs.io/ipfs/" + meta.image.slice(7);
      setNft(meta)
    }

    getNFT()
  }, [metadata])
  console.log("nft:", nft)
  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  }


  async function handleClickMint(tokenId, cid) {

    const transferTx = transferHbar(accountId);
    try {
      const hbartransferReponse = await sendTransaction(transferTx);
      console.log("hbartransferReponse: ", hbartransferReponse)
      if (hbartransferReponse) {
        console.log("Buy Succesful")
        const tokenMinTx = mintToken(tokenId, accountId, cid);
        try {
          const tokenMintResponse = await sendTransaction(tokenMinTx);
          if (tokenMintResponse) {
            console.log("Success")    
          }
        } catch (err) {
          console.log(err)
        }
      }
    } catch (err) {
      console.log(err)
    }

  }

  function transferHbar(accountId) {
    const txID = TransactionId.generate(accountId);

    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addHbarTransfer(accountId, new Hbar(-nft.price))
      .addHbarTransfer(ownId, new Hbar(nft.price))
      .setNodeAccountIds([new AccountId(3)])
      .freeze()

    return transferTransaction;
  } 

  function makeBytes(trans, signingAcctId) {
    const transId = TransactionId.generate(signingAcctId);

    trans.setTransactionId(transId);
    trans.setNodeAccountIds([new AccountId(3)]);

    trans.freeze();

    const transBytes = trans.toBytes();

    return transBytes
  }

  async function sendTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    console.log(tx)
    hashConnectTxBytes = sign ? (
      makeBytes(tx, accountId)
    ) : (
      tx.toBytes()
    );

    response = await props.hash?.sendTransaction(
      props.topic,
      {
        topic: props.topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: accountId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }

  function mintToken(tokenId, accountId, cids) {
    const txID = TransactionId.generate(accountId);

    const mintTx = new TokenMintTransaction()
      .setTransactionId(txID)
      .setTokenId(tokenId)
      .setNodeAccountIds([new AccountId(3)])
      .setMetadata([Buffer.from(cids)])
      .freeze();

    return mintTx;
  }

  if (nft === null) {
    return <div></div>
  } else {
    return (
      <div>
        <motion.section
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
          className="container mx-auto mt-7">
          <Link to="/"><img src={BorrowArrow} /></Link>
        
          <div className="grid grid-cols-1 lg:grid-cols-2 md:grid-cols-1 mt-6 sm:gap-10 sm:grid-cols-1 mb-20">
            <div className="pb-10 sm:pb-0 sm:px-10 sm:py-5 w-full">
              <Card>
                <img
                  src={nft.image}
                  alt="nft img"
                  className="shadow-2xl "
                  style={{ width: "300px", height: "368px", objectFit: "cover" }}
                />
              </Card>
            </div>
            <div className="w-full">
              <div
                className="
               
                text-5xl
                pt-3
                font-bold
                        "
                style={{ color: "red" }}
              >

                {nft.name}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-black
                          text-md
                          sm:text-sm 
                          pt-3
                        ">
            {nft.description}
              </div>
            
              <div
                className="font-general-regular 
                        text-left
                        text-white
                        text-sm
                        sm:text-xl
                        pt-10
                        font-bold
                        ">
                Price
              </div>
              <div className="flex h-20">
                <img src={Price} className="mr-5" />
                <h1 className="text-7xl bold" style={{ color: "#FF0000" }}>{nft.price}</h1>

              </div>
          
              <div className="flex mt-10">
                <button
                  className=" sm:w-full h-10   font-general-medium 
                                      rounded-md shadow-sm text-md text-white w-full font-bold text-xl mr-10
                                    " style={{ backgroundColor: '#FF0000' }}
                // onClick={() => buyNFT()}
                onClick={() => handleClickMint(tokenId, metadata)}
                >
                  PURCHASE
                </button>
                <button
                  className=" sm:w-full h-10 font-general-medium 
                                      rounded-md shadow-sm text-md text-black w-full font-bold text-xl 
                                    "
                  style={{ border: "2px solid black" }}
                // onClick={() => buyNFT()} 
                >
                  SAVE
                </button>

              </div>
            </div>
          </div>

          <Tabs
            value={value}
            variant="fullWidth"
            onChange={handleTabChange}
            TabIndicatorProps={{
              className: classes.indicator,
            }}
          >
            <Tab value={1} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              BUNDLE
            </Typography>} />
            <Tab value={2} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              PROPERTIES
            </Typography>} />

          </Tabs>
          {value === 1 && <div className="grid grid-cols-1 lg:grid-cols-2 md:grid-cols-1 sm:gap-10 bg-black pt-16 pl-32" >
            <div className="">
              <div
                className="
                text-white
                text-3xl
                font-bold
                        ">
                {nft.name}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-white
                          text-white
                          text-md
                          sm:text-sm
                          pt-312
                        ">
              {nft.description}
              </div>
            </div>
            <div className="pb-20">
              <img src={Sixsense} style={{ width: "300px", height: "120px" }} />
            </div>
          </div>}
          {value === 2 && <div className="grid grid-cols-2 p-10" style={{ backgroundImage: `url(${footerback})`, backgroundColor: "black" }}>
            <div className="grid sm:grid-cols-1 lg:grid-cols-2 py-3 sm:gap-2 sm:grid-cols-2 xl:grid-cols-2">
              {propertiy === 0 && (
                <>
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                </>
              )}
              <PropertiesCard
                propertiy={"Background Fire"}
                value={"Hell Ride"}
                key={1}
              />
              <PropertiesCard
                propertiy={"Pistol"}
                value={"WhistleBlower"}
                key={2}
              />
              <PropertiesCard
                propertiy={"Emblem Fire"}
                value={"Fire Pistol"}
                key={3}
              />
              <PropertiesCard
                propertiy={"Fire Stars 3"}
                value={"Rare"}
                key={4}
              />

            </div>
            <div className="ml-20">

              <div
                className="font-general-regular 
                      text-left 
                      text-white
                      text-sm
                      sm:text-xl
                      pt-10
                      font-bold
                      ">
                Details


              </div>
              <div
                className="grid md:grid-cols-2 lg:grid-cols-2 sm:grid-cols-1
                        ">
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  Contract Address :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  <a
                    href="#"
                    className="text-blue-500
                        dark:text-blue-500
                        dark:hover:text-blue-300"
                    target="_blank"
                    rel="noreferrer">
                    0.0.206090
                  </a>
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  Token ID :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  0.0.293823
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Token Standard :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  ERC 721
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Blockchain :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Hedera
                </div>
              </div>
            </div>


          </div>}
        </motion.section>


      </div>

    );
  }

};

export default Buy;


