import { motion } from "framer-motion";
import React, { createContext, useEffect, useRef } from "react"

import NftSlider from "./NftSlider";
import HederaNFTGrid from "../Marketplace/HederaNFTGrid";
import coin from "../../assets/images/56752.png";
import incoin from "../../assets/images/671.png"
import "./banner.css";

import { HaschconnectContext } from "../../App";

const Banner = (props) => {
  const targetRef = useRef();
  const marginTop = props.size * (-0.6);
  const hash = props.hash;
  const minted = props.minted;
  const setMinted = props.setMinted
  const weaponNfts = props.weaponNfts;
  const setWeaponNfts = props.setWeaponNfts;
  useEffect(() => {
    const resizeObserver = new ResizeObserver(entries => {
      for (let entry of entries) {
        const { height } = entry.target.getBoundingClientRect();
        console.log(height)
        props.setSize(height)
      }
    })

    if (targetRef.current) {
      resizeObserver.observe(targetRef.current);
    }
    return () => {
      resizeObserver.disconnect();
    };
  }, [targetRef])

  return (
    <div className="">
      <motion.section
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
        className="
        flex flex-col items-center md:mt-2
                      mt-12 mx-auto
                      border-b
                    dark:border-gray-700
                    border-gray-200
                      p-4 absloute
                      "
      >
        {/* <div style={{backgroundImage: `url(${coin})`, backgroundSize: 'cover', backgroundPosition: "center", display: "flex", position: "fixed"}}>
          <div style={{height: "580px", position: "absolute"}} ></div>
        </div> */}
        {props.inventory ? <img src={incoin} ref={targetRef} /> : <img src={coin} ref={targetRef} />}


        <h1 className="text-white text-6xl bold font-bold text-center item-center mt-20">{props.inventory ? "Inventory" : "Marketplace"}</h1>

      </motion.section>
      {console.log("props.inventory: ", props.inventory)}
      {
        props.inventory ?
          props.accountId ?
            // <div style={{ marginTop: marginTop }} ><NftSlider accountId={props.accountId} minted={props.minted} weaponNfts={props.weaponNfts} setWeaponNfts={props.setWeaponNfts} /></div> :
            // <div style={{ marginTop: marginTop }}>
            <div style={{ marginTop: marginTop }}>
              <HaschconnectContext.Provider value={{ hash, minted, setMinted, weaponNfts, setWeaponNfts }}>
                <NftSlider accountId={props.accountId} topic={props.topic} />
              </HaschconnectContext.Provider> </div> :
            //     <HederaNFTGrid accountId={props.accountId} topic={props.topic} />

            // </div> :
            <motion.h1
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{
                ease: "easeInOut",
                duration: 0.9,
                delay: 0.1,
              }}
              className="
                text-center text-ternary-dark
                dark:text-white
                text-2xl
                sm:text-xs
                pt-20
                font-black
                "
                style={{marginTop: marginTop}}
                >
              Please Connect Wallet First...
            </motion.h1> : null
      }

      {/* <SellCard /> */}
    </div>
  );

};

export default Banner;
