import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Marketplace from "./component/Marketplace/Marketplace";
import AppHeader from "./component/Navbar";
import AppFooter from "./component/Footer";
import Banner from "./component/Banner";
import Buy from "./component/Buy"
import { HashConnect } from "hashconnect";

import { AnimatePresence } from "framer-motion";

import "./App.css";
import { createContext, useState } from "react";
import HederaNFTGrid from "./component/Marketplace/HederaNFTGrid";
import Landing from "./component/Landing/landing";
import Mynft from "./component/Buy/mynft";

export const AccountContext = createContext(null);
export const HaschconnectContext = createContext(null);


function App() {
  const [accountId, setAccountId] = useState("")
  const [hash, setHash] = useState(new HashConnect(true))
  const [topic, setTopic] = useState("")
  const [minted, setMinted] = useState(false);
  const [weaponNfts, setWeaponNfts] = useState([]);
  const [buy, setBuy] = useState(false);
  const [landing, setLanding] = useState(true);
  const [inventory, setInventory] = useState(false);
  const [size, setSize] = useState(0);


  

  let margintTop = size*(-0.6)
  return (
    <AnimatePresence>
      <div
        className="bg-white dark:bg-blue-black duration-300 transition"
        style={{ minHeight: "1024px" }}>
        <Router>

          {/* Connect Wallet */}
          <AccountContext.Provider value={{ accountId, setAccountId, setHash, setTopic, setBuy, setInventory, buy }}>
            <AppHeader  />
          </AccountContext.Provider>



          {/* My Collection */}
          <Routes>
            <Route path="/landing" element={<Landing />} />
            <Route path="/" element={<Marketplace setLanding={setLanding} hash={hash} topic={topic} accountId={accountId} minted={minted} setMinted={setMinted} setAccountId={setAccountId} weaponNfts={weaponNfts} setWeaponNfts={setWeaponNfts} inventory={inventory} setBuy={setBuy} setInventory={setInventory} size={size} setSize={setSize} />} />
            <Route path="/" element={
              <Banner minted={minted} />
            } />
            <Route path="/buy/:ownerId/:accountId/:metadata" element={<Buy setBuy={setBuy} topic={topic} hash={hash} />} />
            <Route path="/inventory/:ownerId/:accountId/:name" element={<Mynft />} />
          </Routes>
          
          {/* NFT Mint */}
          <div  style={{marginTop: margintTop}}>
            {console.log("buy: ", buy)}
          {
            landing ? null : buy ? null : inventory ? null : <HaschconnectContext.Provider value={{ hash, minted, setMinted, weaponNfts, setWeaponNfts }}>
              <HederaNFTGrid accountId={accountId} topic={topic} />
            </HaschconnectContext.Provider>
          }

          </div>

          {/* <AppFooter /> */}
        </Router>
        {/* <ScrollButton /> */}
      </div>
    </AnimatePresence>
  );
}

export default App;
