import { useState, useEffect, useContext } from "react";
import { Link } from "react-router-dom";
import { FiMenu, FiMoon, FiSun, FiX } from "react-icons/fi";
import { motion } from "framer-motion";

import { AccountContext } from "../../App";
import useThemeSwitcher from "../../hooks/useThemeSwitcher";
import { HashConnect } from "hashconnect";

import logoDark from "../../assets/images/v3-removebg-preview-12.png";
import logoLight from "../../assets/images/logo-ct.png";
import walletIcon from "../../assets/images/walletIcon.svg";

let appMetadata = {
  name: "Unreal Integration NFT marketplace",
  description: "Unreal Integration NFT marketplace",
  icon: "https://ipfs.io/ipfs/QmWaYa4TdA67fmr2NrPeiincWFTeLEY7phKwND1pkneGVP"
}

let hashconnect = new HashConnect();

const Navbar = () => {
  const { accountId, setAccountId, setHash, setTopic, setBuy, setInventory, buy } = useContext(AccountContext);
  console.log(accountId)


  //Conenct HashPack Wallet
  async function connectHedera() {
    console.log("connecthedera")
    let initData = await hashconnect.init(appMetadata, "testnet", false);
    console.log(initData)
    setTopic(initData.topic)
    hashconnect.foundExtensionEvent.once((walletMetadata) => {
      hashconnect.connectToLocalWallet(initData.pairingString, walletMetadata);
      console.log("Success to get walletmetadata");
    })

    hashconnect.pairingEvent.once((pairingData) => {
      console.log("Success to get PairingData", pairingData);
      console.log(pairingData)
      console.log("HashPack accountId: ", pairingData.accountIds[0]);
      setAccountId(pairingData.accountIds[0])
    })

    setHash(hashconnect)

  }

  const [activeTheme, setTheme] = useThemeSwitcher();
  const [showMenu, setShowMenu] = useState(false);

  function toggleMenu() {
    if (!showMenu) {
      setShowMenu(true);
    } else {
      setShowMenu(false);
    }
  }

  const Inven =  () => {
    console.log("Inventory")
    setInventory(true)
  }

  return (
    <motion.nav
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      id="nav"
      className="sm:container sm:mx-auto">
      <div className="block py-2 sm:flex sm:items-center sm:justify-between xl:max-w-screen-xl z-10">
        <div className="flex items-center justify-between px-4 sm:px-0">
          <div>
            {activeTheme === "dark" ? (
              <Link to="/">
                <div className="flex hidden sm:block">
                {buy ? <Link to="/"><img src={logoDark} className="w-20" alt="Light Logo"  /></Link> : <img src={logoDark} className="w-20" alt="Light Logo" onClick={() => setInventory(false)} style={{cursor: "pointer"}} /> }
                </div>
              </Link>
            ) : (
                <div className="flex">
                  {buy ? <Link to="/"><img src={logoDark} className="w-20" alt="Light Logo"  /></Link> : <img src={logoDark} className="w-20" alt="Light Logo" onClick={() => setInventory(false)} style={{cursor: "pointer"}} /> }
                </div>
            )}
          </div>

          {/* <button
            onClick={() => setTheme(activeTheme)}
            className="bg-gray-100 block border-gray-500 dark:bg-ternary-dark ml-20 p-2 rounded-xl sm:hidden">
            {" "}
            {activeTheme === "dark" ? (
              <FiMoon className="dark:hover:text-white hover:text-gray-400 text-gray-700 text-xl transition" />
            ) : (
              <FiSun className="hover:text-gray-500 text-white text-xl transition" />
            )}
          </button> */}
{/* 
          <div className="sm:hidden">
            <button
              onClick={toggleMenu}
              type="button"
              className="focus:outline-none"
              aria-label="Hamburger Menu">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                className="dark:text-white fill-current h-7 text-gray-700 w-7">
                {showMenu ? (
                  <FiX className="text-3xl" />
                ) : (
                  <FiMenu className="text-3xl" />
                )}
              </svg>
            </button>
          </div> */}
        </div>


        <div
          className="font-general-medium hidden  items-right justify-center 
                      m-0 mt-5 p-5 shadow-lg sm:flex sm:ml-4 sm:mt-3 sm:p-0 sm:shadow-none">
          {
              buy ? <Link to="/"><h3 className="text-black text-xl font-bold text-right item-center justify-center mt-3 " >MarketPlace</h3></Link> : <h3 className="text-black text-2xl font-bold text-right item-center justify-center mt-3 mr-10 md:mr-20" onClick={() => setInventory(false)}  style={{cursor: "pointer"}}>MarketPlace</h3>
            }

            {
              buy ? <Link to="/"><h3 className="text-black text-2xl font-bold text-right item-center justify-center mt-3 ">Inventory</h3></Link> : <h3 className="text-black text-2xl font-bold text-right item-center justify-center mt-3 ml-20 " onClick={Inven}  style={{cursor: "pointer"}}>Inventory</h3>
            }
          </div>

        <div className="flex-col hidden items-center justify-between sm:flex-row sm:flex">
            
          {/* <button
            onClick={() => setTheme(activeTheme)}
            className="bg-gray-100 block border-gray-500 dark:bg-ternary-dark p-2 rounded-xl">
            {" "}

            {activeTheme === "dark" ? (
              <FiMoon className="dark:hover:text-white hover:text-gray-400 text-gray-700 text-xl transition" />
            ) : (
              <FiSun className="hover:text-gray-500 text-white text-xl transition" />
            )}
          </button> */}

          <div className="hidden md:flex w-40 h-10 mt-4" style={{transform: "skew(-35deg, 0deg)", backgroundColor: "#FF0000", alignItems: "center", justifyContent: "center"}}>
      
              {!accountId ? (
                <button className="flex font-bold text-white"   style={{transform: "skew(35deg, 0deg)"}} onClick={() => connectHedera()}>
                  Connect Wallet
                </button>
              ) : (
                <button
                  className="flex font-bold text-white"
                  style={{transform: "skew(35deg, 0deg)"}}
                // onClick={() => openModal(accountId)}
                >
                

                  {accountId.toString()}
                </button>
              )}
            
          </div>
        </div>
      </div>

    </motion.nav>
  );
};

export default Navbar;