import React from 'react';
import Kommando from "../../assets/images/div.tp-bgimg.png"
import Section from "../../assets/images/Section.png"

import Akira from "../../assets/font/AkiraExpandedDemo.otf"

const Landing = () => {


    return(
        <div>
            <img src={Kommando} style={{height: "1030"}} className='w-full' />
            <div className='' style={{backgroundImage: `url(${Section})`, height: "942px"}}>
                <div className='' style={{paddingTop: "18px", paddingBottom: "18px", paddingRight: "1px", paddingLeft: "1px"}}>
                    <div>
                        <span className='text-8xl font-black text-white' style={{fontFamily: Akira}}>PARTENERS</span>
                    </div>
                    
                </div>
            </div>

        </div>
    )
}

export default Landing;