import { motion } from "framer-motion";
import axios from "axios"
import { useState, useContext, useEffect } from "react";
import { Link } from "react-router-dom";
import { HaschconnectContext } from "../../App";
import { AccountId, TokenMintTransaction, TransactionId, TransactionReceipt, TransferTransaction, Hbar } from "@hashgraph/sdk";


const tokenId = "0.0.2792760"
const ownId = "0.0.2244085"

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
});

  

const NftSingle = ({ name, creator, description, image, accountId, topic, cid, ownerId, price }) => {
  const { hash, minted, setMinted, weaponNfts, setWeaponNfts } = useContext(HaschconnectContext);
  let nfts = weaponNfts;
  console.log("topic: ", topic)
  const Sendnft = (metadata) => {
    var arr = {
      accountId: accountId.toString(),
      metadata: metadata,
    };
    console.log("metadata  ::;  ", metadata)
    if (metadata.length > 0) {
      axios.post("/users/nftsave", arr).then((res) => {
        if (res.data === "success") alert("success");
        else if (res.data === "Already send") alert("Already send");
      });
    }
  };

  async function handleClickMint(tokenId, cid) {
    const newValue = Buffer.from(cid);

    const res = await fetch(`https://ipfs.io/ipfs/${newValue}`);
    const meta = await res.json();
    meta.image = meta.image.slice(7)
    nfts.push(meta);
    console.log("nfts: ", nfts)

    setWeaponNfts(nfts)

    const transferTx = transferHbar();
    try {
      const hbartransferReponse = await sendTransaction(transferTx);
      console.log("hbartransferReponse: ", hbartransferReponse)
      if (hbartransferReponse) {
        console.log("Buy Succesful")
        const tokenMinTx = mintToken(tokenId, accountId, cid);
        try {
          const tokenMintResponse = await sendTransaction(tokenMinTx);
          if (tokenMintResponse) {

            setMinted(!minted)
            Sendnft(nfts)

          }
        } catch (err) {
          console.log(err)
        }
      }
    } catch (err) {
      console.log(err)
    }

  }


  function mintToken(tokenId, accountId, cids) {
    const txID = TransactionId.generate(accountId);

    const mintTx = new TokenMintTransaction()
      .setTransactionId(txID)
      .setTokenId(tokenId)
      .setNodeAccountIds([new AccountId(3)])
      .setMetadata([Buffer.from(cids)])
      .freeze();

    return mintTx;
  }



  async function sendTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    console.log(tx)
    hashConnectTxBytes = sign ? (
      makeBytes(tx, accountId)
    ) : (
      tx.toBytes()
    );

    response = await hash?.sendTransaction(
      topic,
      {
        topic: topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: accountId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }

  function makeBytes(trans, signingAcctId) {
    const transId = TransactionId.generate(signingAcctId);

    trans.setTransactionId(transId);
    trans.setNodeAccountIds([new AccountId(3)]);

    trans.freeze();

    const transBytes = trans.toBytes();

    return transBytes
  }


  function transferHbar() {
    const txID = TransactionId.generate(accountId);

    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addHbarTransfer(accountId, new Hbar(-price))
      .addHbarTransfer(ownId, new Hbar(price))
      .setNodeAccountIds([new AccountId(3)])
      .freeze()


    return transferTransaction;
  } 

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, delay: 1 }}
      transition={{
        ease: "easeInOut",
        duration: 0.7,
        delay: 0.15,
      }}
      style={{ border: "1px solid #313f4a", borderRadius: "10px", boxShadow: "2px 2px 5px #00000040", padding: "20px 15px", position: "relative" }}

    >

      <div
        className="bg-secondary-light cursor-pointer  
                        hover:shadow-xl mb-10 mr-4 ml-4 rounded-xl shadow-lg sm:mb-0 m-1"
      >
        <Link
          to={`/buy/` + ownerId + `/` + accountId + `/` + cid}
          aria-label="Single Project"
        
        >
          <div className="mt-16">
            <img
              src={"https://ipfs.io/ipfs/" + image.slice("7")}
              className="2xl:h-72 h-36 lg:h-44 md:h-48 object-cover rounded-xl sm:h-62 w-full xl:h-56"
              alt="Single Project"
            />
          </div>
          <div className="px-4 py-2">
            <p
              className="dark:text-white font-bold font-general-medium mb-3 mt-3
                            md:text-xl text-center lg:text-2xl text-ternary-dark text-lg"
            >
              {name}
            </p>
            <div className="text-black text-center lg:text-xl mb-2">
              {price}HBAR
            </div>
           

          </div>
        </Link>
        {accountId ? (
          <motion.button
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
              ease: "easeInOut",
              duration: 0.9,
              delay: 0.1,
            }}
            //   className="cursor-pointer bg-transparent bg-gradient-to-r from-purple-700 to-pink-600
            // hover:text-gray-300 px-8 rounded-md"
            style={{
              width: "100%",
              borderRadius: "10px",
              display: "flex",
              fontSize: "18px",
              fontStyle: "normal",
              fontWeight: "600",
              lineHeight: "22px",
              padding: "10px 30px",
              textAlign: "center",
              textShadow: "0 0 4px #0006",
              // transition: ".5s ease",
              // marginBottom: "",
              backgroundColor: "#080810",
              alignItems: "center",
              justifyContent: "center"
            }}
            onClick={() => handleClickMint(tokenId, cid)}
          >
            <motion.h1
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{
                ease: "easeInOut",
                duration: 0.9,
                delay: 0.1,
              }}
              className="font-general-regular 
                            text-center text-white
                            font-black
                            mt-1  
                            "
            >
              BUY NOW
            </motion.h1>
          </motion.button>
        ) : (
          <></>
        )}

      </div>

    </motion.div>
  );
};

export default NftSingle;
