import React, { useState, useEffect, useContext } from "react";
import { motion } from "framer-motion";
import { MetroSpinner } from "react-spinners-kit";
import axios from "axios";
import { AccountId } from "@hashgraph/sdk";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { FiSearch } from "react-icons/fi";
import { HaschconnectContext } from "../../App";
import NftSingle from "../Marketplace/NftSingle";
import InventorySingle from "../Marketplace/InventorySingle";

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
});

const NftSlider = (props) => {  

  console.log("PropsAccountIdL ", props)

  const { hash, minted, setMinted, weaponNfts, setWeaponNfts } = useContext(HaschconnectContext);

  const [getDataLoading, setGetDataLoading] = useState(false);
  const [walletnfts, setWalletNfts] = useState([]);
  const [balance, setBalance] = useState("");
  const [currentNfts, setCurrentNfts] = useState([]);
  const [namesearch, setNameSearch] = useState("");
  const [tokenIdsearch, setTOkenIdSearch] = useState("");
  const [selectType, setSelectType] = useState("");
  const [selectPriceType, setSelectPriceType] = useState("");

  const accountId = AccountId.fromString(props.accountId);

  let value = [],
    metadata = [];

  const [ip, setIp] = useState();

  const getIp = async () => {
    // Connect ipapi.co with fetch()
    const response = await fetch("https://ipapi.co/json/");
    const data = await response.json();

    // Set the IP address to the constant `ip`
    setIp(data.ip);
  };

  // Run `getIP` function above just once when the page is rendered
  useEffect(() => {
    getIp();
  }, []);

  useEffect(() => {
    console.log(ip);
    if (ip) {
      const ws = new WebSocket(`ws://localhost:9000`);

      ws.onopen = function () {
        console.log("Connected to WebSocket server");
      };

      ws.onmessage = function (event) {
        console.log("Message received:", event.data);
        if (accountId) {
          ws.send([accountId, balance]);
        }
      };

      ws.onclose = function () {
        console.log("Disconnected from WebSocket server");
      };

      return () => {
        ws.close();
      };
    }
  }, [accountId, ip, balance]);

  useEffect(() => {
    const load = async () => {
      if (walletnfts.length > 0) {
        console.log("Already imported");
      } else {
        let nftaccounts = await instance.get(`accounts/${accountId}/nfts`);
        let balances = await instance.get(`balances?account.id=${accountId}`);
        setBalance(parseFloat(balances.data.balances[0].balance) / 100000000);
        let nfts = nftaccounts.data.nfts;
        for (var i = 0; i < nfts.length; i++) {
          const currentvalue = nfts[i].metadata;
          const newValue = Buffer.from(currentvalue, "base64");

          const res = await fetch(`https://ipfs.io/ipfs/${newValue}`);
          const meta = await res.json();
          meta.image = meta.image.slice(7);
          value[i] = "https://ipfs.io/ipfs/" + meta.image;
          metadata.push(meta);
        }

        setWalletNfts(value);
        setWeaponNfts(metadata);
        setCurrentNfts(metadata)
      }
      Sendnft(metadata, accountId)
    };

    load();
  }, [accountId, balance, props.minted, weaponNfts.length]);



  const Sendnft = (metadata, accountId) => {
    var arr = {
      accountId: accountId.toString(),
      metadata: weaponNfts,
    };
    console.log("weaponNfts: ", weaponNfts)
    console.log(arr)
    if (weaponNfts.length > 0) {
      axios.post("/users/nftsave", arr).then((res) => {
        if (res.data === "success") alert("success");
        else if (res.data === "Already send") alert("Already send");
      });
    }

  };

  console.log("weaponNfts: ", weaponNfts)

  function handleSearchSubmit() {
    console.log(namesearch, tokenIdsearch, selectType)
    let nfts = [];
    for (var i = 0; i < weaponNfts.length; i++) {
      let text = weaponNfts[i].name.toLowerCase();
      let position = text.search(namesearch.toLowerCase());
      if (position >= 0) {
        if (tokenIdsearch === "") {
          nfts.push(weaponNfts[i])
        } else if (tokenIdsearch && weaponNfts[i].tokenId == tokenIdsearch) {
          nfts.push(weaponNfts[i])
        }
      }
    }
    setCurrentNfts(nfts)
  }

  function handleNameSearch(e) {
    setNameSearch(e.target.value)
  }

  function handleTokenIdSearch(e) {
    setTOkenIdSearch(e.target.value)
  }

  function handleSelectTypeChange(event) {
    console.log(event.target.value);
    let nfts = [];
    for (var i = 0; i < weaponNfts.length; i++) {
      if (event.target.value == "All") {
        nfts.push(weaponNfts[i])
      } else if (weaponNfts[i].weaponType === event.target.value) {
        nfts.push(weaponNfts[i])
      }
    }
    setSelectType(event.target.value)
    setCurrentNfts(nfts)
  }

  function handlePriceSelectTypeChange(event) {
    console.log(event.target.value)

    let nfts = [];
    let prices = [];
    if (event.target.value === "1")
      setCurrentNfts(weapons => [...weapons].sort((a, b) => a.price - b.price));
    else
      setCurrentNfts(weapons => [...weapons].sort((a, b) => b.price - a.price));

    setSelectPriceType(event.target.value)
  }

  return (
    // <>
    //   <motion.section
    //     initial={{ opacity: 0 }}
    //     animate={{ opacity: 1 }}
    //     transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
    //     className="flex flex-col items-center md:mt-12 mx-auto sm:flex-row sm:justify-between"
    //   >
    //     <motion.div
    //       initial={{ opacity: 0, y: -180 }}
    //       animate={{ opacity: 1, y: 0 }}
    //       transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
    //       className="float-right mt-8 sm:mt-0 sm:w-full text-right w-full"
    //     > 
    //       <Slider {...settings} className={weaponNfts.length < 2 ? "mx-3 my-10 slider w-1/3" : "mx-3 my-10 slider"}>
    //         {weaponNfts.map((nft, index) => {
    //           return (
    //             <div className="px-3" key={index}>
    //               <div className="bg-white dark:bg-ternary-dark rounded-xl shadow-lg">
    //                 <div className="slider-img w-full">
    //                   <img
    //                     src={"https://ipfs.io/ipfs/" + nft.image}
    //                     alt=""
    //                     className={weaponNfts.length < 2 ? "rounded-t-xl" : "2xl:h-290 h-290 lg:h-186 md:h-203 object-cover rounded-t-xl sm:h-4 w-full xl:h-242"}
    //                   />
    //                 </div>
    //                 <h1 className="dark:text-white font-bold p-3 text-black text-center">
    //                   SpaceKitty
    //                 </h1>
    //               </div>
    //             </div>
    //           );
    //         })}
    //       </Slider>
    //     </motion.div>
    //   </motion.section>
    // </>
    <section className="mt-5 py-15  md:ml-40 md:mr-40 sm:ml-20 sm:mr-20 sm:mt-10 sm:py-10 ">
      <div
        className="     grid
                        grid-cols-1
                        lg:grid-cols-2
                        2xl:grid-cols-4
                        border-b border-primary-light
                        dark:border-gray-700
                        justify-between
                        pb-3
                        mr-30

                        ">
        <div className="flex w-full pl-7">

          <input
            className="font-general-medium 
                          w-full
                          font-sans
                          md:text-sm
                          border
                          border-slate-950	                         
                          rounded-lg
                          sm:text-[5px]	
                          bg-secondary-light
                          text-primary-dark
                          focus:outline-none
                          w-1/4
                          pl-4
                          "
            id="nftSearch"
            name="nftSearch"
            type="search"
            required=""
            placeholder="SEARCH NFT NAME..."
            aria-label="Name"
            value={namesearch}
            onChange={handleNameSearch}
            style={{ border: "1px solid black", height: "45px" }}
          // onChange={(e) => nftNameFilter(e.target.value)}
          />
          <span
            className="
                sm:block
                bg-primary-light
                dark:bg-ternary-dark
                p-2.5
                shadow-sm 
                rounded-xlP
                cursor-pointer
                " onClick={handleSearchSubmit} style={{ marginLeft: "-52px", marginTop: "-11px" }}>
            <FiSearch className="dark:text-white h-10 text-ternary-dark w-8 mt-1" style={{ color: "#c5c6c6" }}></FiSearch>
          </span>
        </div>
        <div className="flex w-full pl-7">
          <input
            className="font-general-medium 
            w-full
            font-sans
            text-sm
            border
            border-slate-950	                         
            rounded-lg
            sm:text-md
            bg-secondary-light
            text-primary-dark
            focus:outline-none
            w-1/4
            pl-4
            
                "
            id="nftSearch"
            name="nftSearch"
            type="search"
            required=""
            placeholder="SEARCH NFT TOKENID..."
            aria-label="Name"
            value={tokenIdsearch}
            onChange={handleTokenIdSearch}
            style={{ border: "1px solid black", height: "45px" }}
          // onChange={(e) => nftNameFilter(e.target.value)}
          />
          <span
            className="
            sm:block
            bg-primary-light
            dark:bg-ternary-dark
            p-2.5
            shadow-sm 
            rounded-xlP
            cursor-pointer
                " onClick={handleSearchSubmit} style={{ marginLeft: "-52px", marginTop: "-11px" }}>
            <FiSearch className="dark:text-white h-10  w-8 mt-1" style={{ color: "#c5c6c6" }}></FiSearch>
          </span>
        </div>
        <div className="flex w-full pl-7 pb-2.5">
          <select
            className="font-general-medium 
          font-sans
          text-sm
          border
          border-slate-950	                         
          rounded-lg
          sm:text-md
          bg-secondary-light
          text-primary-dark
          focus:outline-none
         w-full
          pl-4 
             
       " style={{ backgroundImage: "url('https://marketplace.digital-arms.com/img/ep_arrow-right-bold.png')", backgroundRepeat: "no-repeat", backgroundPosition: "95% 50%", appearance: "none", border: "1px solid black", height: "45px" }}
            value={selectPriceType}
            onChange={handlePriceSelectTypeChange}
          >
            <option className="sm:text-md text-normal" value="0">PRICE: LOW TO HIGHT</option>
            <option className="sm:text-md text-normal" value="1">PRICE: HIGH TO LOW</option>
          </select>
        </div>

        <div className="flex w-full pl-7">
          <select
            className="font-general-medium 
          font-sans
          text-sm
          border
          border-slate-950	                         
          rounded-lg
          sm:text-md
          bg-secondary-light
          text-primary-dark
          focus:outline-none
        w-full
          pl-4

             
       " style={{ backgroundImage: "url('https://marketplace.digital-arms.com/img/ep_arrow-right-bold.png')", backgroundRepeat: "no-repeat", backgroundPosition: "95% 50%", appearance: "none", border: "1px solid black", height: "45px" }}
            value={selectType}
            onChange={handleSelectTypeChange}
          >
            <option className="sm:text-md text-normal" value="All">ALL ITEMS</option>
            <option className="sm:text-md text-normal" value="Primary">PRIMARY</option>
            <option className="sm:text-md text-normal" value="Secondary">SECONDARY</option>
            <option className="sm:text-md text-normal" value="Special">SPECIAL</option>
            <option className="sm:text-md text-normal" value="Melee">
              MELEE{" "}
            </option>
            <option className="sm:text-md text-normal" value="Explosive">EXPLOSIVE</option>
          </select>
        </div>


      </div>
      {console.log(currentNfts)}
      <div className="grid grid-cols-1  lg:grid-cols-2 md:grid-cols-1 xl:grid-cols-3 2xl:grid-cols-4 mt-6 p-9 sm:gap-10 ">
        {getDataLoading ? (
          <div className="flex justify-center mt-10 w-full">
            <MetroSpinner color="#00ff89" />
          </div>
        ) : currentNfts.length > 0 ? (
          currentNfts.map((metadata, index) => (
            // <HaschconnectContext.Provider value={{ hash, minted, setMinted, weaponNfts, setWeaponNfts }}>
            <InventorySingle
              creator={"@BrainStorm"}
              name={metadata.name}
              image={metadata.image}
              description={metadata.description}
              accountId={props.accountId}
              tokenId={metadata.tokenId}
              key={index}
              ownerId={metadata.tokenId}
              price={metadata.price}
            />
            // </HaschconnectContext.Provider>
          ))
        ) : (
          <div className="flex justify-center align=center mt-10 w-full">
            <MetroSpinner color="#00ff89" />
          </div>
        )}
      </div>
    </section>
  );
};
export default NftSlider;
