import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { motion } from "framer-motion";
import { Link } from "react-router-dom"
import { Skeleton, notification } from "antd";
import "antd/dist/antd.min.css";

import Card from "./nftCard";
import BorrowArrow from "../../assets/images/Rectangle.png"
import Price from "../../assets/images/price.png";
import Sixsense from "../../assets/images/Sixsense.png";
import footerback from "../../assets/images/DOT1.png"

import { Tabs, Tab, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles"
import PropertiesCard from "./propertiesCard";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
  indicator: {
    backgroundColor: 'red', // Customize the color of the tab underline here
    height: "6px"
  },
}));

const Mynft = (props) => {
  const classes = useStyles();

  const [nft, setNft] = useState(null);
  const [value, setValue] = useState(1);
  const [propertiy, setPropertiy] = useState(4);
//   useEffect(() => {
//     props.setBuy(true)
//   })
  const { ownerId, accountId, name } = useParams();
  
  useEffect(() => {
    console.log("getnft function  ")

    const arr = {
        tokenId: ownerId,
        accountId: accountId,
        name: name
    }

    const getNFT = async () => {
        axios.post("/users/GetInventory", arr).then(res => {
            console.log("GETINVETORY: ", res.data, res.data[0].metadata);
            setNft(res.data[0].metadata)
        })
    }

    getNFT()
  }, [ownerId, accountId, name])

  console.log("nft:", nft)
  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  }
  if (nft === null) {
    return <div></div>
  } else {
    return (
      <div>
        <motion.section
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
          className="container mx-auto mt-7">
          <Link to="/"><img src={BorrowArrow} /></Link>
        
          <div className="grid grid-cols-1 lg:grid-cols-2 md:grid-cols-1 mt-6 sm:gap-10 sm:grid-cols-1 mb-20">
            <div className="pb-10 sm:pb-0 sm:px-10 sm:py-5 w-full">
              <Card>
                <img
                  src={"https://ipfs.io/ipfs/" + nft.image}
                  alt="nft img"
                  className="shadow-2xl "
                  style={{ width: "300px", height: "368px", objectFit: "cover" }}
                />
              </Card>
            </div>
            <div className="w-full">
              <div
                className="
               
                text-5xl
                pt-12
                font-bold
                        "
                style={{ color: "red" }}
              >

                {nft.name}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-black
                          text-md
                          sm:text-sm 
                          pt-3
                        ">
            {nft.description}
              </div>
            
              <div
                className="font-general-regular 
                        text-left
                        text-white
                        text-sm
                        sm:text-xl
                        pt-10
                        font-bold
                        ">
                Price
              </div>
              <div className="flex h-20">
                <img src={Price} className="mr-5" />
                <h1 className="text-7xl bold" style={{ color: "#FF0000" }}>{nft.price}</h1>

              </div>
          
             
            </div>
          </div>

          <Tabs
            value={value}
            variant="fullWidth"
            onChange={handleTabChange}
            TabIndicatorProps={{
              className: classes.indicator,
            }}
          >
            <Tab value={1} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              BUNDLE
            </Typography>} />
            <Tab value={2} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              PROPERTIES
            </Typography>} />

          </Tabs>
          {value === 1 && <div className="grid grid-cols-1 lg:grid-cols-2 md:grid-cols-1 sm:gap-10 bg-black pt-16 pl-32" >
            <div className="">
              <div
                className="
                text-white
                text-3xl
                font-bold
                        ">
                {nft.name}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-white
                          text-white
                          text-md
                          sm:text-sm
                          pt-312
                        ">
              {nft.description}
              </div>
            </div>
            <div className="pb-20">
              <img src={Sixsense} style={{ width: "300px", height: "120px" }} />
            </div>
          </div>}
          {value === 2 && <div className="grid grid-cols-2 p-10" style={{ backgroundImage: `url(${footerback})`, backgroundColor: "black" }}>
            <div className="grid sm:grid-cols-1 lg:grid-cols-2 py-3 sm:gap-2 sm:grid-cols-2 xl:grid-cols-2">
              {propertiy === 0 && (
                <>
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                </>
              )}
              <PropertiesCard
                propertiy={"Background Fire"}
                value={"Hell Ride"}
                key={1}
              />
              <PropertiesCard
                propertiy={"Pistol"}
                value={"WhistleBlower"}
                key={2}
              />
              <PropertiesCard
                propertiy={"Emblem Fire"}
                value={"Fire Pistol"}
                key={3}
              />
              <PropertiesCard
                propertiy={"Fire Stars 3"}
                value={"Rare"}
                key={4}
              />

            </div>
            <div className="ml-20">

              <div
                className="font-general-regular 
                      text-left 
                      text-white
                      text-sm
                      sm:text-xl
                      pt-10
                      font-bold
                      ">
                Details


              </div>
              <div
                className="grid md:grid-cols-2 lg:grid-cols-2 sm:grid-cols-1
                        ">
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  Contract Address :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  <a
                    href="#"
                    className="text-blue-500
                        dark:text-blue-500
                        dark:hover:text-blue-300"
                    target="_blank"
                    rel="noreferrer">
                    0.0.206090
                  </a>
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  Token ID :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  0.0.293823
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Token Standard :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  ERC 721
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Blockchain :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Hedera
                </div>
              </div>
            </div>


          </div>}
        </motion.section>


      </div>

    );
  }

};

export default Mynft;


