import { useEffect } from "react";
import Banner from "../Banner";

const Marketplace = (props) => {
  useEffect(() => {
    props.setBuy(false)
    props.setLanding(false)
    // props.setInventory(false)
  })
  return (
    <div className="">

      <Banner hash={props.hash} topic={props.topic} accountId={props.accountId} minted={props.minted} setMinted={props.setMinted} weaponNfts={props.weaponNfts} size={props.size} setWeaponNfts={props.setWeaponNfts} inventory={props.inventory} setSize={props.setSize} />
      {/* <NftGrid /> */}
    </div>
  );
};

export default Marketplace;
